<template>
	<div class="banner">
		<swiper :options="swiperOption" ref="mySwiper" class="commoditySwiper">
			<swiper-slide class="swiper-item" v-for="(item, index) in commodity" :key="index">
				<router-link to="/1"><img class="swiImg" :src="item" /></router-link>
			</swiper-slide>
		</swiper>
		<swiper :options="swiperOption" ref="mySwiper" class="mobileSwiper">
			<swiper-slide class="swiper-item" v-for="(item, index) in mobile" :key="index">
				<router-link to="/1"><img class="swiImg" :src="item" /></router-link>
			</swiper-slide>
		</swiper>
		<div class="swiper-pagination"></div>
	</div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper';
require('swiper/css/swiper.css');
export default {
	name: 'HomeHeader',
	components: {
		swiper,
		swiperSlide
	},
	data() {
		return {
			mobile:[
				'https://liangcang-material.alicdn.com/feupload/e1jib1uagwh9ieoh64s1578701415345.jpg?x-oss-process=image/resize,w_1242/format,webp/interlace,1',
				'https://ykimg.alicdn.com/develop/image/2019-12-18/319426d3921b971c0c7c4df08565f0d4.jpg?x-oss-process=image/resize,w_1242/format,webp/interlace,1',
				'https://liangcang-material.alicdn.com/prod/upload/e9071fdc29684795943b7d8e15dcb9c5.jpg?x-oss-process=image/resize,w_1242/format,webp/interlace,1',
				'https://liangcang-material.alicdn.com/prod/upload/969f74bcd4124820889ecb9358236084.jpg?x-oss-process=image/resize,w_1242/format,webp/interlace,1'
			],
			commodity: [
				'https://liangcang-material.alicdn.com/feupload/488y5hkuq0qeadxqzey1578701418081.jpg?x-oss-process=image/resize,w_2074/interlace,1/quality,Q_80/sharpen,100',
				'https://liangcang-material.alicdn.com/prod/upload/51f95fe0a4274761b1491593626e0f68.jpg?x-oss-process=image/resize,w_2074/interlace,1/quality,Q_80/sharpen,100',
				'https://liangcang-material.alicdn.com/prod/upload/93bcb157fcc1420ba1297089b4794458.jpg?x-oss-process=image/resize,w_2074/interlace,1/quality,Q_80/sharpen,100',
				'https://liangcang-material.alicdn.com/prod/upload/12b0ecd42616419a927b9fe3a9dbcb01.jpg?x-oss-process=image/resize,w_2074/interlace,1/quality,Q_80/sharpen,100'
			],
			swiperOption: {
				effect: this.isMobile() ? 'slide' : 'fade', //切换效果
				fade: {
					crossFade: true
				},
				observer: true, //修改swiper自己或子元素时，自动初始化swiper
				observeParents: true, //修改swiper的父元素时，自动初始化swiper
				//是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
				notNextTick: true,
				//循环
				loop: true,
				//设定初始化时slide的索引
				initialSlide: 0,
				//增加类名'swiper-no-swiping'，使该slide无法拖动，
				noSwiping: false,
				//自动播放
				autoplay: {
					delay: 3000,
					stopOnLastSlide: false,
					disableOnInteraction: false
				},
				//滑动速度
				speed: 300,
				//滑动方向
				direction: 'horizontal',
				pagination: {
					//分页器相关设置都写在这个对象里
					el: '.swiper-pagination',
					bulletElement: 'div',
					clickable: true,
					bulletClass: 'pagination-item',
					bulletActiveClass: 'pagination-item-active'
				}
			}
		};
	},
	mounted() {
		// this.setMouseover();
	},
	methods: {
		setMouseover() {
			let mySwiper = this.$refs.mySwiper.swiper;
			let bullets = mySwiper.pagination.bullets;
			for (let i = 0; i < bullets.length; i++) {
				bullets[i].onmouseover = function() {
					this.click();
				};
			}
		},
		isMobile() {
			let flag = Boolean(
				navigator.userAgent.match(
					/(phone|pod|iPhone|iPod|ios|Android|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
				)
			);
			return flag;
		}
	}
};
</script>

<style lang="scss" scoped="scoped">
.banner {
	width: 100%;
	position: relative;
	.commoditySwiper{
		display: block;
		@media screen and (max-width:480px){
			display: none;
		}
	}
	.mobileSwiper{
		display: none;
		// width: 94%;
		margin: 0 auto;
		@media screen and (max-width:480px){
			display: block;
		}
	}
	.swiper-item {
		background: linear-gradient(rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.3) 20%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.15) 80%, rgba(0, 0, 0, 0.4) 100%) no-repeat center;
		@media screen and (max-width:480px){
			background:transparent;	
		}
		.swiImg {
			width: 100%;
			mix-blend-mode: multiply;
		}
	}
	.swiper-pagination {
		position: absolute;
		overflow: hidden;
		bottom: 30px;
		right: 20px;
		z-index: 5;
		@media screen and (max-width:700px) {
			bottom: 12px;
			right: 50%;
			transform: translateX(50%);
		}
	}
}
</style>
