<template>
	<header>
		<MobileHeader></MobileHeader>
		<HeaderContainer :class="normal"></HeaderContainer>
		<HomeSwiper class="homeSwiper"></HomeSwiper>
	</header>
</template>

<script>
import HomeSwiper from './Heade/HomeSwiper.vue';
import HeaderContainer from './Heade/HeaderContainer.vue';
import MobileHeader from './Heade/MobileHeader.vue';

export default {
	name: 'HomeHeader',
	components: { HomeSwiper, HeaderContainer,MobileHeader },
	data() {
		return {
			normal: ''
		};
	},
	mounted() {
		window.addEventListener('scroll', this.scrollToTop);
	},
	methods: {
		scrollToTop() {
			var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
			if (scrollTop >= 70) {
				this.normal = 'scroll';
			} else if (scrollTop === 0) {
				this.normal = 'restore';
			}
		}
	}
};
</script>

<style lang="scss" scoped>
header {
	width: 100%;
	overflow: hidden;
	.scroll {
		position: fixed;
		top: 0;
		background-color: rgb(42, 45, 59);
	}
	.restore {
		position: 'absolute';
		background-color: transparent;
	}
}
</style>
